body {
	margin: 0;
	padding: 0;
	font-family: "Courier New",-apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
	"Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
	sans-serif;
	background: -webkit-linear-gradient(1deg, #FF52DF 0%, #04C8DE 100%);
	background: linear-gradient(89deg, #FF52DF 0%, #04C8DE 100%);
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}




.centered {
	max-width:500px
}
.bounding-box {
	position: absolute;
	box-shadow: 0 0 0 3px #149df2 inset;
	display: flex;
	flex-wrap: wrap;
	justify-content: center;
	cursor: pointer;
}
.form {
	width:500px;
	background-color:#f5f5f5a8;
}
.particles {
	position:fixed;
	top: 0;
	right:0;
	bottom: 0;
	left: 0;
	z-index:-1;
}
